
.navbar-light .navbar-nav .nav-link:focus, .navbar-light .navbar-nav .nav-link:hover{
    color: #00b3a1;
}

.navbar{
    font-family: 'Roboto Condensed', sans-serif;
}

.logo{
    height: 2em;
}

.fixed-top{
    position: unset;
}

.back-img{
    background-attachment: fixed;
    background-image: url("https://www.generation.org/wp-content/uploads/2014/11/homepage-full-photo.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    max-height: 100%;
    height: 100vh;
    width: 100%;
    color: #f3f3f3b7;
}

.inputs{
    opacity: 0.5;
}


.form-login{
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 1.5em;
    text-align: center;
}

@media only screen 
and (min-width : 1224px) {
 .back-img{
     color: black;
 }
}
 
/* Pantallas grandes */
@media only screen 
and (min-width : 1824px) {
    .back-img{
        color: black;
    }
}


.boton{
    font-size: 10px;
    background-color: #00b3a1;
    text-align: center;
}
.boton:hover{
    background-color: #00B0CF;
}
